import vHeader from "/components/vHeader";
import { strategyList,strategyInfo } from "~/api/zhijianapi";
export default {
  components:{
        vHeader,
  },
  head: {
    title: "指间礼物",
  },
  data() {
    return {
      content: {
        "content": ""
      }, // 富文本内容
      text: "",
      showNews: false,
      
      strategysList: [],
      page: 1,
      limit: 20,
      strategysInfo: "",
      paramId: "",
      isShowTop:true,
      isBack: true
    };
  },
  mounted() {
    this.getStrategyList();

    if (this.$route.query.id) {
      this.paramId = this.$route.query.id;
      this.getStrategyInfo();
    }else{
      this.showNews = false;
    }
    
    if (process.client) {
    }
  },
  methods: {
    getStrategyList() {
      strategyList({
        "action":"strategy",
        "code":"LIST",
        "maxResultCount":this.limit,
        "skipCount":this.page,
        "is_dev":"1"
      }).then(res => {
        if (res.data.sta === 1) {
          this.page = res.data.items.length != 0 ? this.page + 1 : this.page;
          if (this.mobileStatus) {
            this.strategysList = this.strategysList.concat(res.data.items);
          } else {
            this.strategysList = this.strategysList.concat(res.data.items);
          }
        }
        this.$nextTick(() => {
          this.loading = false;
        });
      });
    },
    getStrategyInfo() {
      strategyInfo({
        "action":"strategy",
        "code":"info",
        "id": this.paramId,
        "is_dev":"1"
      }).then(res => {
        if (res.data.sta === 1) {
            this.strategysInfo = res.data.data;
            this.showNews = true;
            this.content.content = res.data.data.content;
            this.text = res.data.data.content;
        }
      });
    },
    moreClick(){
      this.getStrategyList();
    },
    handleHeight(id) {
      /* 由于在vue中不推荐控制dom, 即使我们是在查看属性也是, 确保vue成功定位到dom  并且返回数据 */
      let element = this.$refs["transform_box" + id];
      let height = element.clientHeight;
      return `height: ${height}px; transition: height ${height * 0.0012}s`;
    }
  },
  computed: {
    // 判断当前是否为移动端布局
    mobileStatus() {
      return this.$store.state.isMobile;
    }
  },
   watch: {
    mobileStatus() {
      
    }
  }
};
